<template>
  <div class="m-menu">
    <dl class="nav">
      <dt></dt>
      <dd
        v-for="(item, index) in menuList"
        :key="index"
        @mouseenter="menuEnter(item)"
        @mouseleave="menuLeave"
        @click="showDetail"
      >
        <i :class="item.type"></i>
        {{ item.name }}
        <span class="arrow"></span>
      </dd>
    </dl>
    <div v-if="curDetail" class="detail" @mouseenter="detailEnter" @mouseleave="detailLeave">
      <template v-for="(item, index) in curDetail.items">
        <h4 :key="index" @click="showDetail">{{ item.title }}</h4>
        <span v-for="(v, i) in item.items" :key="v + '_' + i">{{ v }}</span>
      </template>
    </div>
  </div>
</template>

<script>
import api from '@/api/index'
export default {
  data () {
    return {
      curDetail: null,
      // menuList: [
      //   {
      //     title: "",
      //     icon: "food",
      //     children: [
      //       {
      //         title: "",
      //         children: [
      //           "",
      //           "",
      //           "",
      //           "",
      //           "",
      //           "",
      //         ],
      //       },
      //     ],
      //   },
      //   {
      //     title: "",
      //     icon: "takeout",
      //     children: [
      //       {
      //         title: "",
      //         children: [""],
      //       },
      //     ],
      //   },
      //   {
      //     title: "",
      //     icon: "hotel",
      //     children: [
      //       {
      //         title: "",
      //         children: ["", "/", "/", "/"],
      //       },
      //     ],
      //   },
      // ],
      menuList: []
    }
  },
  created () {
    api.getMenuList().then(res => {
      // console.log(res);
      this.menuList = res.data.data
    })
  },
  methods: {
    //   
    menuEnter (item) {
      // console.log(item);
      this.curDetail = item
    },
    // 
    menuLeave () {
      let self = this
      this.timer = setTimeout(() => {
        self.curDetail = null
      }, 3000)
    },
    //  
    detailEnter () {
      clearTimeout(this.timer)
    },
    // 
    detailLeave () {
      this.curDetail = null
    },
    showDetail(){
      this.$router.push({name:'productDetail'})
    }
  }
}
</script>

<style>
</style>



