<template>
  <div class="m-life">
    <el-row>
      <ul class="nav">
        <li><router-link to="/" class="takeout"></router-link></li>
        <li><router-link to="/"></router-link></li>
        <li><router-link to="/"></router-link></li>
        <li><router-link to="/" class="apartment">/</router-link></li>
        <li><router-link to="/" class="business"></router-link></li>
        <li><router-link to="/"></router-link></li>
      </ul>
    </el-row>
    <el-row>
      <el-col :span="14">
        <!-- slide -->
        <slider />
      </el-col>
      <el-col :span="4">
        <div class="m-life-pic"></div>
      </el-col>
      <el-col :span="6">
        <div class="m-life-login">
            <h4><img src="//s0.meituan.net/bs/fe-web-meituan/edcae5e/img/avatar.jpg" /></h4>
            <p class="m-lif-login-name">Hi!{{$store.state.userName ? $store.state.userName : ''}}</p>
            <p v-if="!$store.state.userName">
                <router-link :to="{name:'register'}"><el-button round></el-button></router-link>
            </p>
            <p v-if="!$store.state.userName">
                <router-link :to="{name:'login'}"><el-button round></el-button></router-link>
            </p>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="7">
        <div class="m-life-hotel"></div>
      </el-col>
      <el-col :span="7">
        <div class="m-life-music"></div>
      </el-col>
      <el-col :span="4">
        <div class="m-life-coop"></div>
      </el-col>
      <el-col :span="6">
        <div class="m-life-downapp">
          <img src="//s1.meituan.net/bs/fe-web-meituan/60ac9a0/img/download-qr.png">
          <p>APP</p>
          <p class="last-p"><span class="red">1</span></p>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import slider from './slider.vue'
export default {
  components: { slider }
}
</script>

<style lang="scss">
@import "@/assets/css/index/life.scss";
</style>



