<template>
  <!--   -->
  <div class="page-index">
    <el-row>
      <!--   -->
      <el-col :span="5">
        <!-- menu -->
        <m-menu />
      </el-col>
      <el-col :span="19">
        <!-- life -->
        <life />
      </el-col>
    </el-row>
    <el-row>
      <!--  -->
      <el-col :span="24">
        <!-- container -->
        <container :nav="iStyle" />
      </el-col>
    </el-row>
    <el-row>
      <!--  -->
      <el-col :span="24">
        <!-- container -->
        <container :nav="recomment" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import MMenu from '@/components/index/menu.vue'
import Life from '@/components/index/life.vue'
import Container from '@/components/index/container.vue'

export default {
  components: {
    MMenu,
    Life,
    Container
  },
  data () {
    return {
      iStyle: {
        class: 'istyle',
        title: '',
        list: [
          {
            text: '',
            tab: 'all'
          },
          {
            text: '',
            tab: 'food'
          },
          {
            text: 'SPA',
            tab: 'spa'
          },
          {
            text: '',
            tab: 'movie'
          },
          {
            text: '',
            tab: 'travel'
          }
        ]
      },
      recomment: {
        class: 'youhui',
        title: '',
        list: [{
          text: '',
          tab: 'all'
        }, {
          text: '',
          tab: 'food'
        }, {
          text: '',
          tab: 'xiuxian'
        }]
      }
    }
  }
}
</script>

<style lang='scss'>
@import "@/assets/css/index/index.scss";
</style>



